<template>
    <main>
        <div class="clock">
            <!-- Clock hands -->
            <div class="hand hand-hours"></div>
            <div class="hand hand-minutes"></div>
            <div class="hand hand-seconds"></div>

            <!-- Markings -->
            <div class="marking marking-1">
                <div class="marking-alt"></div>
            </div>
            <div class="marking marking-2">
                <div class="marking-alt"></div>
            </div>
            <div class="marking marking-3">
                <div class="marking-alt"></div>
            </div>
            <div class="marking marking-4">
                <div class="marking-alt"></div>
            </div>
            <div class="marking marking-5">
                <div class="marking-alt"></div>
            </div>
            <div class="marking marking-6">
                <div class="marking-alt"></div>
            </div>
            <div class="marking marking-7">
                <div class="marking-alt"></div>
            </div>
            <div class="marking marking-8">
                <div class="marking-alt"></div>
            </div>
            <div class="marking marking-9">
                <div class="marking-alt"></div>
            </div>
            <div class="marking marking-10">
                <div class="marking-alt"></div>
            </div>
            <div class="marking marking-11">
                <div class="marking-alt"></div>
            </div>
            <div class="marking marking-12">
                <div class="marking-alt"></div>
            </div>
        </div>
    </main>
</template>

<script>
export default {
    methods:{
        hour(){
            document.title="Reloj AnalÃ³gico"
            
            const hoursHand = document.querySelector(".hand-hours");
            const minutesHand = document.querySelector(".hand-minutes");
            const secondsHand = document.querySelector(".hand-seconds");

            const setTime = () => {
            const currentDate = new Date();
            const seconds = currentDate.getSeconds() / 60;
            const minutes = (seconds + currentDate.getMinutes()) / 60;
            const hours = (minutes + currentDate.getHours()) / 12;

            hoursHand.style.setProperty("--rotation", hours * 360);
            minutesHand.style.setProperty("--rotation", minutes * 360);
            secondsHand.style.setProperty("--rotation", seconds * 360);
            };

            setInterval(setTime, 1000);
        }
            
    },
    mounted(){
        this.hour()
        let link = document.createElement("link")
            link.setAttribute("rel","icon")
            link.setAttribute("type","image/png")
            link.setAttribute("href","clock.png")
            document.head.appendChild(link)
    },
    Created(){
    }
}
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

main {
  background: #fbfef9;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.clock {
  background: #191923;
  width: 420px;
  height: 420px;
  border-radius: 50%;
  border: 0.75rem solid #44444f;
  box-shadow: 0 0 16px 0 #2b2c46, inset 4px 4px 16px 0 rgba(90, 86, 102, 0.4),
    inset -4px -4px 16px 0 rgba(90, 86, 102, 0.4);
  position: relative;
}

.clock::after {
  position: absolute;
  content: "";
  background: #aaa9bc;
  border-radius: 50%;
  z-index: 2;
  width: 18px;
  height: 18px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.clock .hand {
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform-origin: bottom;
  border-radius: 5px 5px 0 0;
  z-index: 2;
  --rotation: 0;
  transform: translateX(-50%) rotate(calc(var(--rotation) * 1deg));
}

.clock .hand-hours {
  width: 12px;
  height: 22%;
  background: #44444f;
}

.clock .hand-minutes {
  width: 8px;
  height: 35%;
  background: #44444f;
}

.clock .hand-seconds {
  width: 4px;
  height: 45%;
  background: #cea716;
}

.marking-main {
  margin: auto;
  width: 4px;
  height: 40px;
  background: #7988f3;
}

.marking-alt {
  margin: auto;
  width: 4px;
  height: 20px;
  background: #f8f8ff;
}

.clock .marking {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 1.25rem 0;
  --rotation: 0;
  transform: rotate(var(--rotation));
}

.clock .marking-1 {
  --rotation: 30deg;
}

.clock .marking-2 {
  --rotation: 60deg;
}

.clock .marking-3 {
  --rotation: 90deg;
}

